import { Container } from "pixi.js";
import i18n from "i18next";
import type { mesh } from "pixi.js";
import type { TextField } from "@gdk/core-pixi";

import type { AdaptativeVisualIdentity } from "../../../..";

export class BottomBar extends Container {
	/**
	 * Defines the reference to the background mesh.
	 */
	public bgGray: mesh.NineSlicePlane = null;
	/**
	 * Close button used to close the popin (generated from i18n)
	 */
	public ticketNumber: TextField = null;

	/**
	 * warning for demo mode
	 */
	public logo: Container;

	public constructor(protected vi: AdaptativeVisualIdentity) {
		// PIXI.Container
		super();
	}

	/**
	 * Called by the project loader once the container has
	 * been imported
	 */
	public onLoaded(): void {
		this.ticketNumber = this.getChildByName("ticketNumber");
		this.logo = this.getChildByName("logo");
		this.update();
	}

	/**
	 * update data in the bottom bar with the ticket receiptId if available
	 */
	public update(targetedView: string = ""): void {
		if (
			(targetedView !== "reveal" && targetedView !== "result") ||
			(this.vi.game as any).ticket.demo
		) {
			this.ticketNumber.text = "";
			this.logo.visible = true;
			return;
		}
		this.ticketNumber.text = `<bold>${i18n.t("vi.tickeID.text")}</bold> ${
			(this.vi.game as any).ticket.receiptId
		}`;
		this.logo.visible = false;
	}
}
