import { Container, Graphics, extras, Rectangle, interaction, Sprite } from "pixi.js";
import i18n from "i18next";
import type { Button, TextField } from "@gdk/core-pixi";
import type { mesh } from "pixi.js";

import { EVisualIdentityAction, EVisualIdentityEvent } from "../../../events";
import { EGameEvents } from "../../../..";
import { PurseEes } from "./purseEes";
import type { AdaptativeVisualIdentity } from "../../../..";
import type { InGameView } from "../views/inGameView";

export class SettingsTopBar extends Container {
	/**
	 * Defines the reference to the background mesh.
	 */
	public bgBlue: mesh.NineSlicePlane = null;
	/**
	 * Purse container used to draw the purse amount text (from the editor)
	 */
	public purse: PurseEes = new PurseEes();

	/**
	 * Close button used to close the popin (generated from i18n)
	 */
	public quitButton: Button = null;
	/**
	 * Rules button used to show game's rules (generated from i18n)
	 */
	private rulesButton: Button = null;
	/**
	 * Main rules button (generated from i18n)
	 */
	private winningTableButton: Button = null;
	/**
	 * keep the rules state
	 */
	private rulesAreOpen: boolean = false;
	/**
	 * Sound on button (generated from i18n)
	 */
	public soundOnButton: Button = null;
	/**
	 * Sound off button (generated from i18n)
	 */
	public soundOffButton: Button = null;
	/**
	 * warning for demo mode
	 */
	public demoText: Container;

	public padding: Rectangle = new Rectangle(35, 40, 35, 40);
	public buttonsSpacing: number = 15;

	public constructor(protected vi: AdaptativeVisualIdentity, private parentView: InGameView) {
		// PIXI.Container
		super();
	}

	/**
	 * Called by the project loader once the container has
	 * been imported
	 */
	public onLoaded(): void {
		if (this.vi.parameters.soundButtonEnabled ?? true) {
			this.soundOnButton.setName("settings:soundOn");
			this.soundOnButton.on("pointertap", () => {
				this.vi.emitter.emit(EVisualIdentityAction.SoundEnabled, false);
			});

			this.soundOffButton.setName("settings:soundOff");
			this.soundOffButton.on("pointertap", () => {
				this.vi.emitter.emit(EVisualIdentityAction.SoundEnabled, true);
			});
		} else {
			this.soundOnButton.enabled = false;
			this.soundOffButton.enabled = false;
		}

		this.quitButton.setName("settings:quit");
		this.quitButton.on("pointertap", () => {
			this.parentView.showPopinQuit();
		});

		this.winningTableButton.setName("settings:winningTable");
		this.winningTableButton.on("pointertap", () => {
			this.vi.emitter.emit(EVisualIdentityEvent.ShowWinningTable);
		});

		this.rulesButton.setName("settings:rules");
		this.rulesButton.on("pointertap", () => {
			this.vi.emitter.emit(EVisualIdentityEvent.ShowRules);
		});

		this.vi.game.events.on(EGameEvents.RulesQuit, () => {
			this.rulesAreOpen = false;
			this.enableRulesAndWinningButtons(true);
		});

		this.vi.game.events.on(EGameEvents.RulesOpen, () => {
			this.rulesAreOpen = true;
			this.enableRulesAndWinningButtons(false);
		});

		// Event SettingsEnabled
		this.vi.emitter.on(EVisualIdentityAction.SettingsEnabled, (enabled: boolean) => {
			this.enableRulesAndWinningButtons(enabled);
		});

		this.demoText = this.getChildByName("demoText");
		(this.demoText.getChildByName("label") as TextField).text = i18n.t("vi.demoMode.text");

		this.vi.emitter.on(EVisualIdentityEvent.SoundStatusChanged, () => {
			this.soundOnButton.visible = this.vi.game.soundStatus;
			this.soundOffButton.visible = !this.vi.game.soundStatus;
		});
	}

	/**
	 * Change state of rules and winning table buttons, if not locked
	 */
	public enableRulesAndWinningButtons(enabled: boolean): void {
		// buttons will not be re activate if any popin are still open
		if (
			enabled === true &&
			(this.rulesAreOpen === true ||
				this.parentView.popinQuit.visible === true ||
				this.parentView.popinRealMoney.visible === true)
		) {
			return;
		}
		this.rulesButton.enabled = enabled;
		this.winningTableButton.enabled = enabled;
	}

	/**
	 * Change state of quit game button
	 */
	public enableQuitButton(enabled: boolean): void {
		this.quitButton.enabled = enabled;
	}
}
