import { DockingBounds } from "@gdk/core-pixi";
import type { Sprite } from "pixi.js";
import type { Amount } from "@gdk/core";

import { Purse } from "../../fgs/components/purse";

export class PurseEes extends Purse {
	/**
	 * User Icon
	 */
	public btnUser: Sprite = null;

	/**
	 * Sets the purse amount
	 */
	public set amount(value: Amount) {
		this._amount = value;

		// apply amount text
		this.label.text = this._amount ? this._amount.toString() : "-";

		// resize background
		this.background.width =
			Math.max(this._nineSlice.right, this.label.width + this._padding.left + this._padding.right) +
			this.btnUser.width / 2;
		this.btnUser.docking.x = this.background.width - this.btnUser.width;

		// update viewport
		this.dockingBounds.width = this.background.width;
	}
}
