import { Graphics, utils } from "pixi.js";
import { Amount, EStateEvents } from "@gdk/core";
import { LoadedView, Resize } from "@gdk/core-pixi";
import i18n from "i18next";
import { EGameMode } from "@falcon/bridge";
import type { Button, TextField } from "@gdk/core-pixi";
import type { Container, interaction } from "pixi.js";

import { PurseEes } from "../components/purseEes";
import { SettingsTopBar } from "../components/settingsTopBar";
import { EGameEvents, EVisualIdentityAction, EVisualIdentityEvent } from "../../../..";
import { BottomBar } from "../components/bottomBar";
import type { AdaptativeVisualIdentity } from "../../../..";

export class InGameView extends LoadedView {
	/**
	 * Rules button
	 */
	public rulesButton: Button = null;

	/**
	 * popinRealMoney (popin) (from the editor)
	 */
	public popinRealMoney: Container = null;
	private popinYesButton: Button;
	private popinNoButton: Button;
	protected overlay: Graphics;

	/**
	 * popinQuit
	 */
	public popinQuit: Container = null;
	private popinQuitYesButton: Button;
	private popinQuitNoButton: Button;
	protected overlay2: Graphics;

	/**
	 * Top bar including buttons
	 */
	private settingsTopBar: SettingsTopBar;

	/**
	 * bottom bar with ticket Number
	 */
	private bottomBar: BottomBar;

	/**
	 * Constructor
	 * @param vi: the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		// LoadedView
		super();
		// debugger;
		this.settingsTopBar = new SettingsTopBar(vi, this);
		this.bottomBar = new BottomBar(vi);
	}

	/**
	 * Called once the view is loaded
	 */
	public onLoad(): void {
		// Purse management
		this.vi.emitter.on(EVisualIdentityAction.SetPurse, (purse) => this.setPurse(purse));

		this.vi.game.application.emitter.on("resize", (d) => {
			if (this.parent) {
				this.updateTransform();
			}

			const sizedWith = d.width / this.scale.x;

			this.settingsTopBar.dockingBounds.width = sizedWith;
			this.settingsTopBar.bgBlue.width = sizedWith;

			this.bottomBar.dockingBounds.width = sizedWith;
			this.bottomBar.bgGray.width = sizedWith;
		});

		// init popins
		this.initPopinRealMoney();
		this.initPopinQuit();

		// set up for demo or real money game
		this.getModeIsDemo().then((isDemo) => {
			this.settingsTopBar.demoText.visible = isDemo;
			this.settingsTopBar.purse.visible = !isDemo;
		});

		// listening to changing view
		this.vi.game.events.on(EStateEvents.Change, (targetedView) => {
			this.bottomBar.update(targetedView);

			// only showPopinRealMoney if current view is Result
			if (
				!(this.vi.game as any).resultView ||
				this.vi.game.state.currentView() !== (this.vi.game as any).resultView
			) {
				return;
			}

			// only showPopinRealMoney if targetedView is Home
			if (targetedView !== "home") {
				return;
			}

			// only popinRealMoney in real money mode
			this.getModeIsDemo().then((isDemo) => {
				if (isDemo) {
					this.showPopinRealMoney();
				}
			});
		});
	}

	/**
	 * Create popin to switch to real Money game
	 */
	private initPopinRealMoney(): void {
		// Overlay, will be added behind popup
		this.overlay = new Graphics();

		this.overlay.beginFill(0x0, this.vi.parameters.popinBackgroundOpacity ?? 0.5);
		this.overlay.drawRect(0, 0, 1, 1);
		this.overlay.endFill();
		this.overlay.visible = false;
		this.overlay.resize = new Resize(Resize.cover);
		this.overlay.interactive = true;
		this.addChildAt(this.overlay, 0);

		this.popinRealMoney.visible = false;
		this.popinRealMoney.getChildByName("bgWhite").alpha = this.vi.parameters.popinOpacity ?? 0.75;

		const popinText = this.popinRealMoney.getChildByName("text") as TextField;
		popinText.text = i18n.t("vi.popins.confirmRealMoney.text");

		this.popinYesButton = this.popinRealMoney.getChildByName("realMoney") as Button;
		this.popinYesButton.label = i18n.t("vi.popins.confirmRealMoney.yes");
		this.popinYesButton.on("pointertap", (e: interaction.InteractionEvent) => {
			this.hidePopinRealMoney();
			this.vi.emitter.emit(EVisualIdentityEvent.SwitchGameMode, EGameMode.normal);
			this.settingsTopBar.demoText.visible = false;
			this.settingsTopBar.purse.visible = true;
			(this.vi.game as any).updateGameMode("NORMAL");
		});

		this.popinNoButton = this.popinRealMoney.getChildByName("demo") as Button;
		this.popinNoButton.label = i18n.t("vi.popins.confirmRealMoney.no");
		this.popinNoButton.on("pointertap", (e: interaction.InteractionEvent) => {
			this.hidePopinRealMoney();
		});
	}

	/**
	 * Hide popin
	 */
	private hidePopinRealMoney(): void {
		this.popinRealMoney.visible = false;
		this.overlay.visible = false;
		this.settingsTopBar.enableRulesAndWinningButtons(true);
	}

	/**
	 * Display popin
	 */
	private showPopinRealMoney(): void {
		this.popinRealMoney.visible = true;
		this.overlay.visible = true;
		this.settingsTopBar.enableRulesAndWinningButtons(false);
	}

	/**
	 * Create popin to quit game
	 */
	private initPopinQuit(): void {
		// Overlay, will be added behind popup QUIT
		this.overlay2 = new Graphics();
		this.overlay2.beginFill(0x0, this.vi.parameters.popinBackgroundOpacity ?? 0.5);
		this.overlay2.drawRect(0, 0, 1, 1);
		this.overlay2.endFill();
		this.overlay2.visible = false;
		this.overlay2.resize = new Resize(Resize.cover);
		this.overlay2.interactive = true;
		this.addChildAt(this.overlay2, 0);

		this.popinQuit.visible = false;
		this.popinQuit.getChildByName("bgWhite").alpha = this.vi.parameters.popinOpacity ?? 0.75;

		const popinText = this.popinQuit.getChildByName("text") as TextField;
		popinText.text = i18n.t("vi.popins.confirmQuit.text");

		this.popinQuitYesButton = this.popinQuit.getChildByName("yes") as Button;
		this.popinQuitYesButton.label = i18n.t("vi.popins.confirmQuit.yes");
		this.popinQuitYesButton.on("pointertap", (e: interaction.InteractionEvent) => {
			this.vi.emitter.emit(EVisualIdentityEvent.QuitGame);
			this.hidePopinQuit();
		});

		this.popinQuitNoButton = this.popinQuit.getChildByName("no") as Button;
		this.popinQuitNoButton.label = i18n.t("vi.popins.confirmQuit.no");
		this.popinQuitNoButton.on("pointertap", (e: interaction.InteractionEvent) => {
			this.hidePopinQuit();
		});
	}

	/**
	 * Display popin to confirm that user want to quit game
	 */
	public showPopinQuit(): void {
		this.popinQuit.visible = true;
		this.overlay2.visible = true;
		this.settingsTopBar.enableRulesAndWinningButtons(false);
		this.settingsTopBar.enableQuitButton(false);
	}

	private hidePopinQuit(): void {
		this.popinQuit.visible = false;
		this.overlay2.visible = false;
		this.settingsTopBar.enableRulesAndWinningButtons(true);
		this.settingsTopBar.enableQuitButton(true);
	}

	/**
	 * Test the current Game Mode
	 */
	public async getModeIsDemo(): Promise<boolean> {
		const mode: EGameMode = await this.vi.game.com.getGameMode();
		return mode === EGameMode.demo;
	}

	/**
	 * Sets the purse
	 * @param purse the purse to set
	 */
	public setPurse(value: Amount): void {
		this.settingsTopBar.purse.amount = new Amount(value);
	}

	/**
	 * get the resize method with EESTI parameters
	 *
	 */
	public getResizeFunction(type: string = ""): Resize {
		if (type === "cover") {
			return new Resize(Resize.boundedCover(65, 65, 768, this));
		} else {
			return new Resize(Resize.boundedFitContain(82, 72, 768));
		}
	}
}
