import i18next from "i18next";
import type { Graphics } from "pixi.js";
import type { TextArea } from "@gdk/core-pixi";

import * as fgs from "../../fgs";

export class LoadingView extends fgs.LoadingView {
	/**
	 * Bottom text above the bottom background (from the editor)
	 */
	protected disclaimer: TextArea = null;
	private revealLogoMask: Graphics;

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		this.visible = true;
		this.disclaimer = this.getChildByName("disclaimer");
		this.disclaimer.text = i18next.t("vi.loading.disclaimer");
		this.revealLogoMask = this.getChildByName("logoTopMask");
		this.getChildByName("logoTop").mask = this.revealLogoMask;
	}

	/**
	 * Defines the function called each time a file is loaded
	 * @param percentage the percentage of files already loaded
	 */
	public onProgress(percentage: number): void {
		this.percentage.text = `<numbers>${percentage}</numbers>%`;
		this.revealLogoMask.docking.position.x = -1024 + (1024 * percentage) / 100;
	}
}
